.container {
  box-sizing: border-box;
  cursor: text;
  overflow-y: auto;
  height: 200px;
  padding: 4px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: 0;
}

.container *,
.container *::before,
.container *::after {
  box-sizing: inherit;
}

.container.container--focused {
  border-color: #0096cc;
}

.container.container--errors {
  border-color: #db3d44;
}

.container .token-list .token {
  float: left;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  max-width: 100%;
  height: 32px;
  line-height: 1.5;
  white-space: nowrap;
  margin: 0 4px 4px 0;
  padding: 0;
  border: 0;
  border-radius: 4px;
  color: #0096cc;
  background-color: #cceaf5;
  cursor: default;
}

.container .token-list .token:hover {
  background-color: #e6f4fc;
}

.container .token-list .token.token--error {
  color: #db3d44;
  background-color: #f9b5b5;
}

.container .token-list .token.token--error:hover {
  background-color: #ffdada;
}

.container .token-list .token.token--editable:hover {
  cursor: pointer;
}

.container .token-list .token.token--active .autosized-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  margin: 4px 8px;
  height: 32px;
}

.container .token-list .token.token--active .autosized-wrapper input {
  height: auto;
  border-bottom: 1px solid #aaa;
}

.container .token-list .token.token--read-only:hover {
  background-color: #cceaf5;
}

.container .token-list .token.token--read-only.token--error:hover {
  background-color: #f9b5b5;
}

.container .token-list .token .token__label-wrapper {
  flex: 1 0 0;
  margin: 0 8px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.container .token-list .token .token__delete-button {
  flex: 0 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 100%;
  padding: 2px 8px;
  cursor: pointer;
  opacity: 0.3;
  border-left: 1px solid #aaa;
  border-radius: 0 4px 4px 0;
}

.container .token-list .token .token__delete-button:hover {
  opacity: 1;
  background-color: #aaa;
}

.container .token-list .token .token__delete-button:hover .token__delete-button__close-icon:before, .container .token-list .token .token__delete-button:hover .token__delete-button__close-icon:after {
  border-color: #fff;
}

.container .token-list .token .token__delete-button .token__delete-button__close-icon {
  width: 14px;
  height: 14px;
  position: relative;
}

.container .token-list .token .token__delete-button .token__delete-button__close-icon:before, .container .token-list .token .token__delete-button .token__delete-button__close-icon:after {
  content: '';
  height: 14px;
  left: 6px;
  border-left: 2px solid #222;
  position: absolute;
}

.container .token-list .token .token__delete-button .token__delete-button__close-icon:before {
  transform: rotate(-45deg);
}

.container .token-list .token .token__delete-button .token__delete-button__close-icon:after {
  transform: rotate(45deg);
}

.container .autosized-wrapper {
  float: left;
  overflow: hidden;
  margin-right: 4px;
}

.container .autosized-wrapper input {
  height: 32px;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  cursor: text;
  box-shadow: none;
  background: none;
  color: #222;
}
